.form {
  padding-bottom: 55px;
}

.form__block {
  @include margin-reset;
  border: none;
  margin-bottom: 39px;
}

.form__block-legend {
  @include margin-reset;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  color: $green-color1;
  margin-bottom: 25px;
  position: relative;
  display: flex;
  text-transform: uppercase;

  & span {
    background-color: $main-color;
    z-index: 2;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    top: 50%;
    left: 0;
    z-index: 1;
    background-color: $green-color5;
  }
}

.form__block-legend--type,
.form__block-legend--color,
.form__block-legend--data {
  margin-bottom: 32px;
}

.form__block-legend--color {
  margin-bottom: 26px;
}

.form__block-legend--data {
  margin-bottom: 29px;
}

.form__block-legend--tel {
  margin-bottom: 4px;
}

.form__block-legend--email {
  margin-bottom: 3px;
}

.form__block-legend--plus {
  margin-bottom: 23px;
}

.form__block-legend--type span {
  padding-right: 25px;
}

.form__block-legend--color span {
  padding-right: 15px;
}

.form__block-legend--data span {
  padding-right: 20px;
}

.form__block-legend--tel span {
  padding-right: 28px;
}

.form__block-legend--email span {
  padding-right: 19px;
}

.form__block-legend--plus span {
  padding-right: 21px;
}

.form__list {
  @include list-reset;
}

.form__label {
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  color: $title-color;
}

.form__item--radio {
  margin-bottom: 19px;
}

.form__item--radio:last-child {
  margin-bottom: 0;
}

.form__radio-name,
.form__checkbox-name {
  padding-left: 60px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
}

.form__radio + label::before {
  content: "";
  width: 18px;
  height: 18px;
  border: 9px solid $main-color;
  box-shadow: 0 0 0 2px $text-color;
  border-radius: 50%;
  background-color: $main-color;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  box-sizing: content-box;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form__radio:checked + label::before {
  background-color: $green-color5;
}

.form__radio:checked:hover + label::before,
.form__radio:hover + label::before {
  box-shadow: 0 0 0 2px $green-color5;
}

.form__radio:checked:focus + label::before,
.form__radio:focus + label::before {
  box-shadow: 0 0 0 2px $grey-color15;
}

.form__radio:focus ~ label {
  outline: -webkit-focus-ring-color auto 5px;
}

.form__radio:-moz-focusring ~ label {
  outline: 2px dotted;
}

.form__radio:checked:disabled + label::before,
.form__radio:disabled + label::before,
.form__checkbox:disabled + .form__check,
.form__checkbox:checked:disabled + .form__check {
  opacity: 40%;
}

.form__item--checkbox {
  margin-bottom: 31px;
}

.form__item--checkbox:last-child {
  margin-bottom: 0;
}

.form__check {
  width: 38px;
  height: 38px;
  position: absolute;
  border: 2px solid $text-color;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form__checkbox:checked + .form__check {
  background-image: url("../img/tick-icon.svg");
  background-size: 23px 16px;
  background-repeat: no-repeat;
  background-position: center center;
}

.form__checkbox:hover + .form__check,
.form__checkbox:checked:hover + .form__check {
  border: 2px solid $green-color5;
}

.form__checkbox:focus + .form__check,
.form__checkbox:checked:focus + .form__check {
  border: 2px solid $grey-color13;
}

.form__checkbox:focus ~ .form__check {
  outline: -webkit-focus-ring-color auto 5px;
}

.form__checkbox:-moz-focusring ~ .form__check {
  outline: 2px dotted;
}

.form__textarea,
.form input[type="text"],
.form input[type="tel"],
.form input[type="email"] {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 19px;
}

.form__textarea {
  border: solid 2px $title-color;
  padding: 17px;
  width: 100%;

  &:hover {
    border: solid 2px $green-color5;
  }

  &:focus {
    color: $text-color;
  }
}

.form input[type="text"],
.form input[type="tel"],
.form input[type="email"] {
  border: none;
  border-bottom: solid 2px $title-color;

  &:hover {
    border-bottom: solid 2px $green-color5;
  }

  &:focus {
    color: $text-color;
  }
}

.form input[type="text"]::-webkit-input-placeholder,
.form input[type="email"]::-webkit-input-placeholder,
.form input[type="tel"]::-webkit-input-placeholder,
.form__textarea::placeholder {
  color: $grey-color5;
  font-size: 17px;
  line-height: 18px;
}

.form input[type="text"]::-moz-placeholder,
.form input[type="email"]::-moz-placeholder,
.form input[type="tel"]::-moz-placeholder,
.form__textarea::placeholder {
  color: $grey-color5;
  font-size: 17px;
  line-height: 18px;
}

.form input[type="text"]::placeholder,
.form input[type="tel"]::placeholder,
.form input[type="email"]::placeholder,
.form__textarea::placeholder {
  color: $grey-color5;
  font-size: 17px;
  line-height: 18px;
}

.form__block--data,
.form__block--tel,
.form__block--email {
  margin-bottom: 12px;
}

.form__block--additional {
  margin-bottom: 7px;
}

.form__group {
  display: flex;
  width: 100%;
  flex-direction: column;
  margin-bottom: 22px;
}

.form__line {
  flex-grow: 1;
  padding: 17px 9px 12px 0;
  background-color: transparent;
}

.form__column {
  display: flex;
  width: 100%;
  position: relative;

  & .form__line-icon {
    position: absolute;
    bottom: 20px;
  }
}

.form__block--tel .form__line-icon {
  right: 7px;
}

.form__block--email .form__line-icon {
  right: 3px;
}

.form__block--tel .form__line {
  padding: 14px 50px 16px 0;
}

.form__block--email .form__line {
  padding-right: 50px;
}

.form__line--email {
  padding-bottom: 13px;
}

.form__line-icon {
  fill: $title-color;
  opacity: 0.2;
}

.form__line:hover ~ .form__line-icon,
.form__line:focus ~ .form__line-icon {
  fill: $green-color5;
  opacity: 1;
}

.form__line--tel:hover ~ .form__line-icon,
.form__line--tel:focus ~ .form__line-icon {
  opacity: 1;
}

.form__button-wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 30px;
}

.form__button {
  order: 2;
}

.form__sup {
  font-size: 14px;
  line-height: 16px;
  margin-top: 0;
  margin-bottom: 30px;
  order: 1;
}

@media (min-width: $tablet-width) {
  .form {
    padding-bottom: 50px;
  }

  .form__block {
    padding: 59px 0 61px 0;
    margin-bottom: 0;
  }

  .form__block-container {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }

  .form__block-legend {
    grid-column: 1/2;
    grid-row: 1/2;
    background-color: transparent;

    & span {
      background-color: transparent;
      padding-top: 9px;
    }

    &::after {
      display: none;
    }
  }

  .form__item--radio {
    margin-left: 5px;
    margin-bottom: 47px;
  }

  .form__radio + label::before {
    width: 20px;
    height: 20px;
    border: 13px solid $main-color;
  }

  .form__radio-name {
    padding-left: 75px;
  }

  .form__checkbox-name {
    padding-left: 82px;
  }

  .form__label {
    font-size: 19px;
  }

  .form__block-legend--color span {
    padding-right: 0;
  }

  .form__list {
    grid-column: 2/3;
    grid-row: 1/2;
  }

  .form__block--color {
    background-color: $grey-color2;
    padding-bottom: 14px;
  }

  .form__list--color {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .form__check {
    width: 52px;
    height: 52px;
  }

  .form__checkbox:checked + .form__check {
    background-size: 32px 22px;
  }

  .form__item--checkbox {
    width: 48%;
    margin-bottom: 47px;
  }

  .form__item--checkbox:last-child {
    margin-bottom: 47px;
  }

  .form__block--data {
    padding-top: 49px;
    padding-bottom: 30px;
  }

  .form__block-legend--data span,
  .form__block-legend--tel span,
  .form__block-legend--email span,
  .form__block-legend--plus span {
    padding-top: 0;
  }

  .form__block-legend--data,
  .form__block-legend--tel,
  .form__block-legend--email,
  .form__block-legend--plus {
    padding-top: 21px;
  }

  .form__group {
    flex-direction: row;
    margin-bottom: 22px;
    align-items: center;
  }

  .form__line {
    margin-bottom: 0;
    padding: 15px 9px 15px 0;
  }

  .form__line-name {
    margin-right: 25px;
  }

  .form input[type="text"]::placeholder,
  .form input[type="tel"]::placeholder,
  .form input[type="email"]::placeholder,
  .form textarea::placeholder {
    font-size: 19px;
  }

  .form input[type="email"],
  .form input[type="tel"],
  .form input[type="text"] {
    padding-left: 18px;
  }

  .form__block--tel {
    background-color: $grey-color2;
    padding-top: 49px;
    padding-bottom: 12px;
  }

  .form__block--email {
    padding-top: 12px;
    padding-bottom: 49px;
    background-color: $grey-color2;
  }

  .form__block--tel .form__group,
  .form__block--email .form__group {
    margin-bottom: 0;
  }

  .form__block--tel .form__line-icon {
    right: 26px;
  }

  .form__block--email .form__line-icon {
    right: 22px;
  }

  .form__column .form__line-icon {
    bottom: 17px;
  }

  .form__block--additional {
    padding-top: 50px;
    padding-bottom: 53px;
  }

  .form__button-wrapper {
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    padding-right: 50px;
    padding-left: 50px;
  }

  .form__button {
    width: 230px;
  }

  .form__sup {
    margin-bottom: 0;
    font-size: 17px;
    line-height: 30px;
  }
}

@media (min-width: $desktop-width) {
  .form {
    grid-column: 1/-1;
  }

  .form__block-container {
    grid-template-columns: 1fr 4fr 1fr;
  }

  .form__block {
    padding-top: 57px;
  }

  .form__block--type {
    padding-top: 44px;
  }

  .form__block--data {
    padding-top: 50px;
  }

  .form__block--tel {
    padding-top: 48px;
  }

  .form__block--email {
    padding-top: 13px;
  }

  .form__block--additional {
    padding-top: 51px;
    padding-bottom: 48px;
  }

  .form__block-legend {
    padding-left: 61px;
  }

  .form__list {
    grid-column: 2/4;
    justify-content: flex-start;
  }

  .form__group-wrapper,
  .form__group {
    grid-column: 2/3;
  }

  .form__item--checkbox {
    width: 30%;
  }

  .form__column .form__line-icon {
    bottom: 15px;
    right: 4px;
  }

  .form__block--tel .form__line-icon {
    right: 8px;
  }

  .form__button-wrapper {
    padding-right: 225px;
    padding-left: 225px;
    max-width: 1150px;
    margin-left: auto;
    margin-right: auto;
  }
}
